class utilsModule {
    
    constructor() {

    }

    /** 检查是否移动端 */
    public isMobile() {
        const pattern = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|OperaMini/i;
        return pattern.test(navigator.userAgent);
    }

    /**
     * 数组中随机取几个元素
     * @param array 数组
     * @param count 元素个数
     */
    public getRandomArrayElements<T>(array: Array<T>, count: number) {
        /** 数组长度 */
        let length = array.length;
        /** 最小长度 */
        let min = length - count, temp, range;
        while (length-- > min) {
            range = Math.floor((length + 1) * Math.random());
            temp = array[range];
            array[range] = array[length];
            array[length] = temp;
        }
        return array.slice(min);
    }

    /**
    * 随机打乱数组
    * @param array
    */
    public shuffleArray<T>(array: Array<T>) {
        for (let i = array.length - 1; i >= 0; i--) {
            let randomIndex = Math.floor(Math.random() * (i + 1));
            let itemAtIndex = array[randomIndex];
            array[randomIndex] = array[i];
            array[i] = itemAtIndex;
        }
        return array;
    }

    /**
     * 范围随机数
     * @param min 最小数
     * @param max 最大数
     */
    public ranInt(min: number, max: number) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    /**
     * 插入js标签
     * @param path `src`路径
     * @param callback 加载成功
     */
    public insertScript(path: string, callback?: Function) {
        var label = document.createElement('script');
        label.src = path;
        label.onload = function () {
            if (callback) callback();
            if (label.parentNode) label.parentNode.removeChild(label);
        }
        document.body.appendChild(label);
    }
}

/** 工具模块 */
const uiils = new utilsModule();

export default uiils;